<template>
	<view class="content">
		<button type="default" @tap="uploadFile">上传文件</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				uploadFilePath: ''
			}
		},
		onLoad() {
			// uni.request({
			// 	url: "https://fcsapi.yozodocs.com/composite/upload",

			// })
		},
		methods: {
			uploadFile() {
				// 
				uni.chooseFile({
					"count": 1,
					"success": (res) => {
						console.log(res.tempFilePaths,"文件路径返回成功")
						this.uploadFilePath = res.tempFilePaths[0];
						console.log(this.uploadFilePath)
					},
					"fail": (res) => {
						console.log(res,"文件路径返回失败")
					},
					complete: () => {
						uni.uploadFile({
							url:"https://fcsapi.yozodocs.com/composite/upload",
							filePath:this.uploadFilePath,
							name:"file",
							formData: {
								"convertType " : 61
							},
							success: (data) => {
								console.log(JSON.parse(data.data).data.viewUrl)
							}
						})
					}
				})
				// uni.uploadFile()
			}
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>
